      </vs-input>
      <vs-input class="w-full mt-5 mb-4" label-placeholder="Adresse" v-model="quoteData.client_adr">
      </vs-input>
      <vs-input class="w-full mt-5 mb-4" label-placeholder="Téléphone" v-model="quoteData.client_tel">
      </vs-input>
      <vs-input type="number" class="w-full mt-5 mb-4" label-placeholder="Total HT" v-model="quoteData.total_ht">
      </vs-input>
      <vs-input type="number" class="w-full mt-5 mb-4" label-placeholder="Total Net HT" v-model="quoteData.total_net_ht">
      </vs-input>
      <vs-input type="number" class="w-full mt-5 mb-4" label-placeholder="Avance" @change="Changedvance" v-model="quoteData.advance">
      </vs-input>
      <vs-input type="number" class="w-full mt-5 mb-4" label-placeholder="Reste" v-model="quoteData.rest">
      </vs-input>
      <label for="items">Statut</label>
      <v-select v-model="quoteData.quote_status" :options="['En attente','Confirmé', 'Annulé ']" class="mb-3">

      </v-select>
      <label for="items">Produits</label>
      <v-select disabled id="items" multiple :closeOnSelect="false" v-model="itemsDataOrderedItems" :options="itemsDataOrderedItems" label="item" :dir="$vs.rtl ? 'rtl' : 'ltr'" /><br>
      <div class="mt-4">
          <vs-button icon-pack="feather" class="mr-4" @click="PutQuote">Valider</vs-button>
          <vs-button icon-pack="feather" color="warning" @click="activePromptEditQuote = false">Annuler</vs-button>
      </div>
