                <p>{{ invoiceDetails.invoiceNo }}</p>

                <h6 class="mt-4">ARIANA LE</h6>
                <p>{{ invoiceDetails.invoice }}</p>
                <h5 class="mt-5 mb-6">{{ companyDetails.name }}</h5>
                <h6 class="mt-4">Date de début </h6>
                <p>{{ invoiceDetails.invoiceNo }}</p>
                <h6>Date de fin</h6>
                <p>{{ invoiceDetails.invoiceNo }}</p>
              </div>
              <div class="vx-col w-full  mt-12">
