<script>
import moduleDataList from "@/store/data-list/moduleDataList.js"
import vSelect from 'vue-select'
import axios from 'axios'

export default {
  components: {
    'v-select': vSelect,
  },
  data() {
    return {
      activePromptPrintQuote: false,
      mailTo: "",
      companyDetails: {
        name: 'Société Afrah Bhar',
        addressLine1: 'Ariana',
        zipcode: '2080',
        mailId: 'bhar.events@gmail.com',
        mobile: '70 319 178',
      },
      recipientDetails: {
        fullName: 'Peter Stark',
        addressLine1: '8577 West West Drive ',
        addressLine2: 'Holbrook, NY',
        zipcode: '90001',
        mailId: 'peter@mail.com',
        mobile: '+91 988 888 8888',
      },
      invoiceDetails: {
        invoiceNo: '001/2019',
        invoiceDate: 'Mon Dec 10 2018 07:46:00 GMT+0000 (GMT)',
      },
      invoiceData: {
        tasks: [
          {
            id: 1,
            task: 'Website Redesign',
            hours: 60,
            rate: 15,
            amount: 90000,
          },
          {
            id: 2,
            task: 'Newsletter template design',
            hours: 20,
            rate: 12,
            amount: 24000,
          },
        ],
        subtotal: 114000,
        discountPercentage: 5,
        discountedAmount: 5700,
        total: 108300,
      },
      nbTest: 0,
      activePromptEditQuote: false,
      quoteData: {},
      itemsData: [],
      itemsDataOrdered: [],
      itemsDataOrderedItems: [],
      quotes: [],
      selected: [],
      // quotes: [],
      itemsPerPage: 10,
      isMounted: false,

      // Data Sidebar
      addNewDataSidebar: false,
      sidebarData: {},
    }
  },
  computed: {
    currentPage() {
      if (this.isMounted) {
        return this.$refs.table.currentx
      }
      return 0
    },
    queriedItems() {
      return this.$refs.table ? this.$refs.table.queriedResults.length : this.quotes.length
    }
  },
  methods: {
    activePromptPrint() {
      this.activePromptPrintQuote = true
    },
    printInvoice() {
      window.print()
    },
    async GetQuotes() {
      await axios
        .get("/quotes/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then((res) => {
          this.quotes = res.data;
        });
    },
    deleteData(id) {
      this.$store.dispatch("dataList/removeItem", id).catch(err => { console.error(err) })
    },
    GetQuote(quoteData) {
      if (this.nbTest == 0 ) {
        this.quoteData = quoteData
        this.itemsData = this.quoteData.itemsData
        this.itemsData.map((item) => {
          this.itemsDataOrderedItems.push(item.orderedItemData)
        })
        this.activePromptEditQuote = true
        this.nbTest = 1
      }
      else {
        this.quoteData = []
      }

    },
    Changedvance() {
      this.quoteData.rest = this.quoteData.total_net_ht - this.quoteData.advance
    },
    async PutQuote() {
      this.itemsData.map((item) => {
        this.quoteData.items.push(item.id)
      })
      await axios
        .put("/quotes/" + this.quoteData.id + "/", this.quoteData, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: "Validation avec succès",
            color: "success",
          });
          this.GetQuotes();
          this.activePromptEditQuote = false;
        })
        .catch(() => {
          this.$vs.notify({
            title: "Erreur de modification",
            text: "veuillez vérifier vos informations",
            iconPack: "feather",
            icon: "icon-alert-circle",
            color: "danger",
          });
          this.activePromptEditQuote = true;
        });
    },
    DeleteQuote(tr) {
      this.quoteData = tr
      this.$vs.dialog({
        type: "confirm",
        color: "danger",
        title: `Confirmation de la suppression`,
        text: `Supprimer "${tr.number}"`,
        acceptText: "Supprimer",
        accept: this.ConfirmDeleteQuote,
        cancelText: "Annuler",

      });
    },
    async ConfirmDeleteQuote() {
      await axios
        .delete("/quotes/" + this.quoteData.id + "/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: "Suppression avec succès",
            color: "success",
          });
          this.GetQuotes();
          this.activePromptEditquote = false;
        })
        .catch(() => {
          this.$vs.notify({
            title: "Suppression avec Suppression",
            text: "veuillez essayer plus tard",
            iconPack: "feather",
            icon: "icon-alert-circle",
            color: "danger",
          });
          this.activePromptEditRealStock = true;
        });
    },
    PrintQuote(tr) {
      this.$router.push('/imprimer-devis')
    },
    getOrderStatusColor(status) {
      if (status == 'En attente') return "warning"
      if (status == 'Confirmé') return "success"
      if (status == 'Annulé') return "danger"
      return "primary"
    },
    getPopularityColor(num) {
      if (num > 90) return "success"
      if (num > 70) return "primary"
      if (num >= 50) return "warning"
      if (num < 50) return "danger"
      return "primary"
    },
    toggleDataSidebar(val = false) {
      this.addNewDataSidebar = val
    }
  },
  created() {
    if (!moduleDataList.isRegistered) {
      this.$store.registerModule('dataList', moduleDataList)
      moduleDataList.isRegistered = true
    }
  },
  mounted() {
    this.isMounted = true;
    this.$emit("setAppClasses", "invoice-page")
    this.GetQuotes()
  }
}
</script>

